.sidebar{
    background: $color-1;
    font-family: $font-1;
    height: 100%;
    min-height: $minHeight;
    min-width: 264px;
    border-radius: $borderRadius;
    position: relative;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 250px 1fr 108px;
    grid-template-areas: "1" "2" "3";

    .id{
        grid-area: 1;
        text-align: center;
        user-select: none;
        position: relative;

        .idContent{
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
            width: 100%;
            h3{
                font-size: 1.3rem;
                text-transform: uppercase;
                letter-spacing: 2rem;
                margin-top: 1.5rem;
                color: $color-5;
            }
            
        }
    }
    .navigation{
        grid-area: 2;
        padding: 2rem 1rem;
        ul{
            height: 100%;
            display: grid;
            li{
                padding: 1rem 0 1rem .4rem;
                font-size: 1.3rem;
                position: relative;
                &:hover i {
                    transform: translate(3px, 3px);
                }
                a{
                    color: $color-5;
                }
                .active{
                    color: $color-4 ;

                }
                i{
                    position: absolute;
                    font-size: 0.95rem;
                    transform: rotateY(3px);
                    transition: .2s ease;
                }
                span{
                    margin-left: 2.7rem;
                }
            }
        }
    }
}
.socialNetwork{
    grid-area: 3;
    ul{
        display: flex;
        width: 264px;
        padding: 0 14px;
        justify-content: space-between;
        li{
            font-size: 1.5rem;
            background: $color-5;
            color: $color-1;
            border-radius: 50%;
            padding: .5rem .8rem;
            transition: 0.9s;
            cursor: pointer;
            a:visited{
                color: $color-1;
            }
            &:nth-child(1){
                padding: .5rem .85rem;
            }
            &:nth-child(4){
                padding: .5rem .75rem;
            }
            &:hover{
                background: $color-4;
            }
        }
    }
    .signature{
        margin-top: 2rem;
        text-align: center;
        letter-spacing: .3rem;
        font-size: .9rem;
        p{
            color: $color-5;
            & :hover{
                color: $color-4;
            }
        }
    }
}
.monPorofil{
    width: 100px;
    height: 100px;
    border-radius: 50%;
    margin-bottom: 1rem;
    margin-top: 2rem;
    box-shadow: 0 0 0 4px $color-3, 0 0 0 8px $color-4, 0 0 0 12px $color-2;
} 
.nomProfil{
    font-size: 1.3rem;
    text-transform: uppercase;
    letter-spacing: 0.5rem;
    margin-top: 4rem !important;
    color: $color-5 !important;
}
//Quand la taille de l'écran est réduite
@media screen and (max-width: 850px){
    .sidebar{
        display: flex;
        min-height: 50px;
        margin-bottom: .2rem;
        .id, .socialNetwork{
           display: none;
        }
        .navigation{
            width: 100%;
            padding: .1rem 1rem;

            ul{
                display: flex;
                justify-content: space-between;
                span{
                    margin-left: -55px;
                }
            }
        }
    }
}
//Pour les écrans mobiles 
@media screen and (max-width: 500px) {
    .sidebar{
        ul{
            justify-content: space-around !important;
            li{
                padding: 1rem 0 !important;
            }
            i{
                font-size: 1.4rem;
                transform: translateY(-3px) !important;
            }
            span{
                display: none;
            }
        }
    }
}